<template>
    <v-container class="pa-2" fluid>
        <v-row>
            <v-col v-for="(item, i) in items" :key="i">
                <v-card :color="item.color">
                    <v-list-item three-line>
                        <v-list-item-avatar size="50" tile>
                            <v-img :src="item.src"></v-img>
                        </v-list-item-avatar>
                        <v-list-item-content>
                            <div class="txt">
                                <div class="coin">{{item.coin}}</div>
                                <div class="stats">{{item.price}}&nbsp;&nbsp;<span :class="item.fclr">({{item.changein}}%)</span></div>
                                <div class="profit" v-if="item.coin!='Bitcoin (BTC)'">PROFIT ${{item.profit}}</div>
                            </div>
                        </v-list-item-content>
                    </v-list-item>
                    <div class="footer">
                        <div>#{{item.rank}}</div>
                        <div>${{item.volume}}</div>
                    </div>
                </v-card>
            </v-col>
        </v-row>
        <div style="padding-bottom:72px"></div>
        <v-footer fixed class="portfolio" color="#2E3032">
            <v-col class="text-center" cols="12" @click.prevent="pageRefresh">
                <strong>${{this.portfolio.toFixed(2).replace(/\B(?=(\d{3})+(?!\d))/g, ",")}}</strong>
                <div style="font-size:smaller;">${{this.totalmarket}}</div>
            </v-col>
        </v-footer>
    </v-container>
</template>

<script>
export default {
    data: () => ({
        items: [],
        hold: [{
                name: "electroneum",
                quantity: 210805.03,
                imageurl: 'https://s2.coinmarketcap.com/static/img/coins/200x200/2137.png',
                lstname: 'Electroneum (ETN)',
                color: '#0F1317'
            },
            {
                name: "tomochain",
                quantity: 2020.103,
                imageurl: 'https://s2.coinmarketcap.com/static/img/coins/200x200/2570.png',
                lstname: 'TomoChain (TOMO)',
                color: '#0F1317'
            },
            {
                name: "bitcoin",
                quantity: 0,
                imageurl: 'https://s2.coinmarketcap.com/static/img/coins/200x200/1.png',
                lstname: 'Bitcoin (BTC)',
                color: '#0F1317'
            },
        ],
        portfolio: 0,
        totalmarket: '',
        decimals:5
    }),
    mounted() {
        this.getdata();
    },
    methods: {
        getdata() {
            this.hold.forEach(el => {
                this.axios.get(`https://api.coinmarketcap.com/v1/ticker/${el.name}/?ref=widget&convert=USD`).then((response) => {
                    this.items.push({
                        color: el.color,
                        src: el.imageurl,
                        coin: el.lstname,
                        price: parseFloat(response.data[0]["price_usd"]).toFixed(this.decimals),
                        fclr: Number(response.data[0]["percent_change_24h"]) < 0 ? 'loss' : 'gain',
                        changein: Number(response.data[0]["percent_change_24h"]),
                        profit: parseFloat(response.data[0]["price_usd"]).toFixed(this.decimals) * el.quantity,
                        rank: response.data[0].rank,
                        volume: this.currency(response.data[0]["24h_volume_usd"], 2)
                    });
                    this.portfolio = this.portfolio + (parseFloat(response.data[0]["price_usd"]).toFixed(this.decimals) * el.quantity);
                })
            });
            this.axios.get(`https://api.coinmarketcap.com/v2/global/`).then((response) => {
                this.totalmarket = this.currency(response.data.data.quotes.USD["total_market_cap"],2);                
            });
        },
        pageRefresh(){
            document.location.reload();
        },        
        currency(t, e) {
            e = Math.pow(10, e);
            for (var a = ["K", "M", "B", "T"], i = a.length - 1; i >= 0; i--) {
                var r = Math.pow(10, 3 * (i + 1));
                if (r <= t) {
                    1e3 == (t = Math.round(t * e / r) / e) && i < a.length - 1 && (t = 1, i++), t += " " + a[i];
                    break
                }
            }
            return t
        }
    }
}
</script>

