body{
    background-color: black;
}

.txt{
  font-family: 'Roboto';
  padding-left:10px;
  line-height: 24px;
}

.coin{
  color:#6D7579;
  font-size: 22px;
}

.stats{  
  color:#6D7579;
  font-size: 18px;
}

.profit{  
  color:#6D7579;
  font-size: 14px;
}

.footer{
  justify-content: space-between;
  display: flex;
  padding-left: 10px;
  padding-right: 10px;
  padding-bottom:5px;
  font-family: 'Roboto';
  color:#6D7579;
}

.portfolio{
  font-family: 'Roboto';
  text-align: center;
  color:#FAFAFA !important;
}

.gain{
    color:#29D3A5 !important;
}

.loss{
    color:#FF4D29 !important;
}
</style>